/*This is beta version*/
define('button-group',['require','jquery','./declare','./widget'],function(require) {

    var $ = require('jquery'),
        declare = require('./declare'),
        Widget = require('./widget');
    /**
     * Button
     *
     *		new ButtonGroup({
     *			childs:[]
     *		});
     * @module ui.button-group
     * @extends Widget
     * @class Simple.ButtonGroup
     */
    var ButtonGroup = declare('ButtonGroup', Widget, {

        name: 'ButtonGroup',

        /**
         * 
         * @property items
         * @type Array
         * @default []
         */
        childs: [],

        /**
         * 
         * @property template
         * @type String
         * @reayOnly
         */
        template: '<div class="sui-button-group"></div>',

        renderUI: function() {

            this.renderChilds();

            var children = this.getChildren();
            if (children.length < 2) {
                return false;
            }
            children.forEach(function(child,index){
                if (index === 0) {
                    child.el.addClass('sui-button-first');
                }
                if (index === children.length - 1) {
                    child.el.addClass('sui-button-last');
                }
            });
        },
        /**
         * 
         * @method enable
         * @param {Int} index 
         */
        enable: function(index) {
            var button = this.getChildren()[index];
            button.enable();
        },
        /**
         * 
         * @method disable
         * @param {Int} index 
         */
        disable: function(index) {
            var button = this.getChildren()[index];
            button.disable();
        },
        /**
         * 
         * @method setFocus
         * @param {Int} index 
         */
        focus: function(index) {
            var button = this.getChildren()[index];
            button.focus();
            this._currentFocus = focus;
        },
        /**
         * 
         * @method getFocus
         * @return {HTMLElement} button
         */
        getFocus: function() {
            return this._currentFocus;
        }
    });
    return ButtonGroup;
});
